import { CompareResult } from './types.js';
import { clean, compareVersion, validateWithError } from './utils.js';

/**
 * @description
 * Determines if version `verA` is less than version `verB`.
 *
 * @param verA - The first version to compare.
 * @param verB - The second version to compare.
 * @returns `true` if `verA` is less than `verB`, `false` otherwise.
 *
 * @docsCategory preference/utils/semver
 * @docsPage semver-lt
 * @codeFilter lt
 */
export const lt = (verA: string, verB: string): boolean => {
  if (validateWithError(verA, verB)) {
    return compareVersion(clean(verA), clean(verB)) === CompareResult.LessThan;
  }
  return false;
};
